import React, { Children } from "react";
import  ReactDOM  from "react-dom/client";
import Header from "../src/component/Header";
import SlideBar from "../src/component/SlideBar";
import Body from "../src/component/Body";
import { Provider, useSelector } from "react-redux";
import store from "../src/utility/store";
import { Outlet, RouterProvider, createBrowserRouter } from "react-router-dom";
import VideoWatchFeed from "../src/component/VideoWatchFeed";
import SearchedVideosResult from "../src/component/SearchedVideosResult";
import Shorts from "../src/component/Shorts";



const App=()=>{

    return <>
        <Provider store={store} >
            <Header />
            <SlideBar />
            <Outlet  />
  
        </Provider>
    
    </>
}

const router=createBrowserRouter([
 {
    path:'/',
    element:<App  />,
    children:[
        {
            path:'/',
            element:<Body/>,
            
        },
        {
            path:'/watchvideo/:id',
            element:<VideoWatchFeed/>
        },
        {
            path:'/search/:query',
            element:<SearchedVideosResult/>
        },
        {
            path:'/shorts',
            element:<Shorts/>
        }
    ]
        
    
 }  
])


const root= ReactDOM.createRoot(document.getElementById('root'));
root.render(<RouterProvider router={router}/>)